import React from 'react'
import styled from 'styled-components'
import { menuData } from '../data/MenuData'
import { Container } from '../globalStyle'
import Logo from '../img/logo.svg'

const Nav = styled.nav`
    min-height: 104px;
    background: #000;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 100;
`

const NavWrapper = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
`

const NavMenu = styled.div`
    @media screen and (max-width: 768px) {
        display: none;
    }
`

const NavMenuLink = styled.a`
    color: #fff;
    text-decoration: none;
    font-weight: 700;
    margin-right: 32px;
    outline: none;
    :last-child {
        margin-right: 0;
    }
`

const MenuBars = styled.i`
    display: none;

    @media screen and (max-width: 768px) {
        display: block;
    }
`

// const LanguageToggle = styled.a`
// `

const Navbar = () => {
    return (
        <Nav>
            <Container>
                <NavWrapper>
                    <img src={Logo} alt="swim system"/>
                    <NavMenu>
                    {
                        menuData.map((item, index) => (
                            <NavMenuLink key={index} href={item.anchor}>
                                {item.title}
                            </NavMenuLink>
                        ))
                    }
                    </NavMenu>
                    <MenuBars/>
                </NavWrapper>
            </Container>
        </Nav>
    )
}

export default Navbar